.App {
  /* text-align: center; */
  background-color: #f7f9fe !important;
  min-height: 100vh;
  position: relative;
  z-index: 1;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
.overlay.corona-shape-bg {
  background-image: url(./assets/images/corona-shape-bg.png);
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: 100%;
}
.api-used-section {
  border-top: 1px solid #dddddd;
  text-align: center;
  margin-top: 25px;
  padding: 15px 0 15px 0;
}
.api-used-section p {
  font-size: 13px;
  margin: 0;
  padding: 6px 0 6px 0;
  color: #616a81;
}
.api-used-section a {
  color: #616a81;
  text-decoration: none;
  padding-left: 3px;
}
.api-used-section a:hover {
  text-decoration: underline;
}
.made-with {
  color: #616a81;
  margin: 0;
  padding: 0;
  font-size: 13px;
}
.made-with .fa {
  -webkit-animation: heartbeat 1s infinite;
  animation: heartbeat 1s infinite;
  transition: all .2s ease-in-out;
}
@keyframes heartbeat
{
    0%
    {
      transform: scale( .75 );
    }
    20%
    {
      transform: scale( 1 );
      color: red;
    }
    40%
    {
      transform: scale( .75 );
    }
    60%
    {
      transform: scale( 1 );
      color: red;
    }
    80%
    {
      transform: scale( .75 );
    }
    100%
    {
      transform: scale( .75 );
    }
}